const baseState = {
  data: ["NORMAL", "LOCK", "FREEZE", "INVILD"],
  get: function (s) {
    return s === "LOCK" ? "" :
      s === "NORMAL" ? "" :
        s === "FREEZE" ? "" :
          s === "INVILD" ? "" :
            s === "DEL" ? "" :
              ""
  }
}

//
const interfaceTypes = {
  data: ["RESTFUL", "WEBSERVICE", "JAR", "JNI", "OTHER"],
  get: function (s) {
    return s === "RESTFUL" ? "restful" :
      s === "WEBSERVICE" ? "webservice" :
        s === "JAR" ? "jar" :
          s === "JNI" ? "JNI" :
            s === "OTHER" ? "" :
              ""
  }
}

//
const authSafety = {
  data: ["I", "II", "III", "IV"],
  get: function (s) {
    return s === "I" ? "I" :
      s === "II" ? "II" :
        s === "III" ? "III" :
          s === "IV" ? "IV" :
            ""
  }
}

export {
  baseState,
  interfaceTypes,
  authSafety
}
